@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;500&display=swap);
/* ? base classes */

* {
  font-family: "Quicksand", sans-serif;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: 100%;
}

.input {
  border: none;
  border-radius: 50px;
  padding: 0.4rem;
  margin-right: 0.4rem;
  outline: none;
}

.footer {
  border-top-left-radius: 50px;
  border-top-right-radius: 50px;
}

.container {
  width: 90%;
  margin: auto;
  padding: 1rem 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

nav {
  color: #f8f8f8;
  font-family: "Nunito", sans-serif;
  position: relative;
}

.title {
  font-weight: 700;
  color: white;
  font-size: 2rem;
}

.links {
  margin-left: 1rem;
  color: white;
}

.cta-1 {
  margin-right: 1.2rem;
  color: #f8f8f8;
  background-color: #4f6ef7;
  padding: 0.6rem 1.4rem;
  border-radius: 0.2rem;
  font-size: 0.8rem;
  font-weight: bold;
  transition: all 300ms ease-in;
}

.cta-2 {
  color: #4f6ef7;
  padding: 0.6rem 1.4rem;
  border: 1px solid #4f6ef7;
  border-radius: 0.2rem;
  font-size: 0.8rem;
  font-weight: bold;
  transition: all 300ms ease-in;
}

.cta-1:hover {
  color: #4f6ef7;
  background-color: #f8f8f8;
}

.cta-2:hover {
  color: #4f6ef7;
  background-color: #f8f8f8;
  border: none;
}

.bars {
  display: none;
  align-self: center;
}

.sidenav {
  width: 100%;
  background-color: rgb(95, 192, 38);
  z-index: 4;
  position: absolute;
  left: -999%;
  transition: 560ms ease;
}

.sidenav.active {
  left: 0;
  transition: 560ms ease;
}

.img-res {
  width: 20%;
  height: 50px;
}

@media screen and (max-width: 780px) {
  .cta-1,
  .cta-2,
  .links {
    display: none;
  }

  .bars {
    display: block;
  }
}

.info__container {
    width:80%;
    margin: 3rem auto;
}

.info__title {
    text-align: center;
}

.info__about {
    text-align: center;
    margin: 1rem auto;
}
.address__container {
  width: 90%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 3rem;
  gap: 3rem;
  justify-items: center;
  align-content: center;
}

.address {
  margin: 1rem 0;
  padding: 1rem 0;
}

.address__card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.address__card div:nth-child(2) {
  text-align: center;
  margin: 0.6rem 0;
  line-height: 2rem;
}

@media screen and (max-width: 780px) {
  .address__container {
    grid-template-columns: 1fr;
  }
}

.form {
  background-color: #90d5ec;
background-image: linear-gradient(315deg, #90d5ec 0%, #fc575e 74%);
  height: 100vh;
}

